@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap);
.app__header{display:flex;justify-content:space-between}.app__title{font-weight:bold;font-size:28px;line-height:37px}.app__title span{color:#9f0013}.app__menu ul{display:flex;justify-content:space-between;font-weight:bold;font-size:24px;line-height:32px}.app__menu ul li{margin:0 8px}.app__menu ul li a:hover{color:#9f0013}
.randomchar{display:grid;grid-template-columns:repeat(2, 50%);align-items:center;box-shadow:5px 5px 20px rgba(0,0,0,.25)}.randomchar__block{padding:40px 35px;display:grid;grid-template-columns:180px auto;grid-column-gap:30px;-webkit-column-gap:30px;column-gap:30px}.randomchar__img{width:180px;height:180px;object-fit:cover}.randomchar__info{display:grid;grid-template-rows:minmax(29px, auto) 90px 38px;grid-row-gap:10px;row-gap:10px;padding-top:3px}.randomchar__name{font-weight:bold;font-size:22px;line-height:29px;text-transform:uppercase}.randomchar__descr{font-size:14px;line-height:18px}.randomchar__btns a:nth-child(1){margin-right:30px}.randomchar__static{padding:40px 35px;background-color:#232222;position:relative}.randomchar__static button{margin-top:13px}.randomchar__title{font-weight:bold;font-size:24px;line-height:32px;letter-spacing:-0.045em;color:#fff}.randomchar__title:nth-child(2){margin-top:33px}.randomchar__decoration{position:absolute;bottom:14px;right:-37px}
.char__content{margin-top:50px;display:grid;grid-template-columns:650px 425px;grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px;align-items:start}.char__grid{display:grid;grid-template-columns:repeat(3, 200px);grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px;grid-row-gap:30px;row-gap:30px}.char__item{width:200px;height:318px;background-color:#232222;box-shadow:5px 5px 10px rgba(0,0,0,.25);padding:15px;cursor:pointer;transition:.3s transform}.char__item img{width:200px;height:200px;object-fit:cover;-webkit-transform:translate(-15px, -15px);transform:translate(-15px, -15px)}.char__item_selected{box-shadow:0 5px 20px #9f0013;-webkit-transform:translateY(-8px);transform:translateY(-8px)}.char__name{font-weight:bold;font-size:22px;line-height:29px;text-transform:uppercase;color:#fff}
.skeleton__header{display:grid;grid-template-columns:40px auto;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;align-items:center}.skeleton__circle{width:40px;height:40px;background-color:#c4c4c4;border-radius:100%}.skeleton__mini{width:100%;height:16px;background-color:#c4c4c4}.skeleton__block{height:35px;width:100%;background-color:#c4c4c4;margin-top:15px}
.char__info{padding:25px;box-shadow:0px 0px 20px rgba(0,0,0,.25);position:relative;z-index:5;background-color:#fff}.char__info-name{font-weight:bold;font-size:22px;line-height:29px;text-transform:uppercase}.char__info .skeleton{margin-top:30px}.char__basics{display:grid;grid-template-columns:150px auto;grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px}.char__basics img{width:150px;height:150px;object-fit:cover}.char__btns{margin-top:35px}.char__btns a:nth-child(2){margin-top:10px}.char__descr{margin-top:15px;font-size:14px;line-height:18px}.char__comics{font-weight:bold;font-size:18px;line-height:24px;margin-top:10px}.char__comics-list{position:relative;margin-top:10px}.char__comics-item{width:100%;padding:0px 10px;line-height:25px;box-shadow:0px 4px 4px rgba(0,0,0,.25);margin-top:10px}.char__select{font-weight:bold;font-size:18px;line-height:24px;text-align:center}
.button{min-width:101px;color:#fff;text-align:center;text-transform:uppercase;font-size:14px;transition:.3s transform;border:none;background-color:transparent;cursor:pointer}.button__long{display:block;width:170px;margin:45px auto 0 auto}.button .inner{position:relative;background-color:#9f0013;line-height:18px;padding:0 18px;transition:none}.button .inner::before{border-color:#9f0013 transparent;border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;left:0;top:-10px;transition:none}.button .inner::after{border-color:#9f0013 transparent;border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;right:0;bottom:-10px;-webkit-transform:rotate(180deg);transform:rotate(180deg);transition:none}.button__main:hover,.button__secondary:hover{color:#fff}.button__secondary .inner{background-color:#5c5c5c}.button__secondary .inner::before{border-color:#5c5c5c transparent;transition:none}.button__secondary .inner::after{border-color:#5c5c5c transparent;transition:none}.button::before{content:"";display:block;height:10px;margin-left:10px;transition:none}.button::after{content:"";display:block;height:10px;margin-right:10px;transition:none}.button.button__main::before,.button.button__main::after{background-color:#9f0013}.button.button__secondary::before,.button.button__secondary::after{background-color:#5c5c5c}.button:hover{-webkit-transform:translateY(-5px);transform:translateY(-5px)}.button:disabled{-webkit-filter:grayscale(0.5);filter:grayscale(0.5)}*{font-family:"Roboto Condensed",sans-serif;margin:0;padding:0;box-sizing:border-box}ul,li,dl{list-style-type:none;margin:0;padding:0}h1,h2,h3,h4,h5,h6{margin:0}a{text-decoration:none;color:inherit;display:inline-block}a:hover{text-decoration:none;color:inherit}p{margin:0;padding:0}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.app{width:1100px;margin:0 auto;padding:50px 0 50px 0;position:relative}.app .bg-decoration{position:absolute;right:-174px;bottom:-70px;z-index:-1}main{margin-top:50px;position:relative}.pulse{-webkit-animation:pulse 1.5s ease-in-out .5s infinite;animation:pulse 1.5s ease-in-out .5s infinite}@-webkit-keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}@keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}
